p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

button {
  cursor: pointer;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

@keyframes move {
  0% {
    transform: translateY(100%)rotate(90deg);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes pulsing {
  0% {
    transform: scale(.9)rotate(5deg);
  }

  25% {
    transform: scale(1)rotate(10deg);
  }

  50% {
    transform: scale(1.1)rotate(15deg);
  }

  75% {
    transform: scale(1)rotate(10deg);
  }

  100% {
    transform: scale(.9)rotate(5deg);
  }
}

@keyframes lunge {
  0% {
    transform: scale(.4)translateY(100%);
  }

  100% {
    transform: scale(1)translateY(0);
  }
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}

